.pagination-container {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
 

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding: 40px 0;
  }

  @media screen and (min-width: 768px) {
    padding: 50px 0 60px 0;
  }
}

.pagination-button:not(:last-child),
.dots {
  margin-right: 16px;
}

.pagination-button {
  padding: 0;
  background-color: #ffffff;
  border: none;
  color: #000000;
  cursor: pointer;
  font-family: Roboto;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.333;
  background: inherit;
  color: inherit;
  
  // width: 40px;
  // height: 40px;
}

.pagination--current {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background-color: #ff6b08;
  color: #ffffff;
}

.dots,
.first-button,
.last-button {
  @media screen and (max-width: 768px) {
    display: none;
  }
}

.arrow-left,
.arrow-right {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  background-color: #f7f7f7;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  fill: #000000;
  stroke: #000;
  background-position: center;
  background-repeat: no-repeat;
}

.arrow-left {
  background-image: url('../images/arrow-left.svg');
}

.arrow-right {
  background-image: url('../images/arrow-right.svg');
}
